import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Sai Charan"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.kobweb_portfolio.pages.homePage() }
        ctx.router.register("/about") { org.example.kobweb_portfolio.pages.AboutPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text",
                org.example.kobweb_portfolio.Styles.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text",
                org.example.kobweb_portfolio.Styles.SubheadlineTextStyle)
        ctx.theme.registerStyle("nav-header", org.example.kobweb_portfolio.Styles.NavHeaderStyle)
        ctx.theme.registerStyle("git-hub-button",
                org.example.kobweb_portfolio.Styles.GitHubButtonStyle)
        ctx.theme.registerStyle("hover-button",
                org.example.kobweb_portfolio.Styles.HoverButtonStyle)
        ctx.theme.registerStyle("elevation-button",
                org.example.kobweb_portfolio.Styles.ElevationButtonStyle)
        ctx.theme.registerStyle("section-title",
                org.example.kobweb_portfolio.Styles.SectionTitleStyle)
        ctx.theme.registerStyle("description", org.example.kobweb_portfolio.Styles.DescriptionStyle)
        ctx.theme.registerStyle("badge-text", org.example.kobweb_portfolio.Styles.BadgeTextStyle)
        ctx.theme.registerStyle("project-card",
                org.example.kobweb_portfolio.Styles.ProjectCardStyle)
        ctx.theme.registerStyle("page-content",
                org.example.kobweb_portfolio.Styles.PageContentStyle)
        ctx.theme.registerStyle("profile-name",
                org.example.kobweb_portfolio.Styles.ProfileNameStyle)
        ctx.theme.registerStyle("salutation", org.example.kobweb_portfolio.Styles.SalutationStyle)
        ctx.theme.registerStyle("about-me", org.example.kobweb_portfolio.Styles.AboutMeStyle)
        ctx.theme.registerStyle("about-me-column",
                org.example.kobweb_portfolio.Styles.AboutMeColumnStyle)
        ctx.theme.registerStyle("project-image",
                org.example.kobweb_portfolio.Styles.ProjectImageStyle)
        ctx.theme.registerStyle("designation", org.example.kobweb_portfolio.Styles.DesignationStyle)
        ctx.theme.registerStyle("company", org.example.kobweb_portfolio.Styles.CompanyStyle)
        ctx.theme.registerStyle("work", org.example.kobweb_portfolio.Styles.WorkStyle)
        ctx.theme.registerStyle("button", org.example.kobweb_portfolio.Styles.ButtonStyle)
        ctx.theme.registerStyle("markdown",
                org.example.kobweb_portfolio.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("footer",
                org.example.kobweb_portfolio.components.sections.footer.FooterStyle)
        ctx.theme.registerVariant("-circle", org.example.kobweb_portfolio.Styles.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored",
                org.example.kobweb_portfolio.Styles.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("bounce", org.example.kobweb_portfolio.Styles.BounceKeyframes)
        ctx.theme.registerKeyframes("side-menu-slide-in",
                org.example.kobweb_portfolio.Styles.SideMenuSlideInAnim)
        org.example.kobweb_portfolio.initColorMode(ctx)
        org.example.kobweb_portfolio.initTheme(ctx)
        org.example.kobweb_portfolio.Styles.initSiteStyles(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        org.example.kobweb_portfolio.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
